@media (max-width: 1200px) {
  // --------------------------------- Banner
  .banner__inner {
    justify-content: space-around;
  }

  .banner__slider-list {
    max-width: 600px;
  }

  .banner__image {
    min-height: 380px;
    max-width: 600px;
  }

  // ----------------------------- Catalog
  .catalog__inner-list {
    justify-content: space-around;
  }

  .catalog__inner-item {
    .product-item__wrapper {
      margin: 0;
    }
  }
}

@media (max-width: 1120px) {
  // --------------------------------Header
  .menu__item:not(:last-child) {
    margin-right: 20px;
  }

  .header__adress {
    margin-right: 20px;
  }
}

@media (max-width: 1020px) {
  // --------------------------------Header
  .menu__item:not(:last-child) {
    margin-right: 0;
  }

  .header__adress {
    margin-right: 0;
  }
}

@media (max-width: 968px) {
  // --------------------------------Header
  .page__wrapper {
    padding: 0;
  }

  .header__top-inner {
    gap: 36px;
  }

  .header__bottom {
    display: none;
  }

  .menu {
    display: none;
  }

  .menu-mobile {
    display: block;
  }

  .menu__item {
    font-size: 20px;
    font-weight: 700;
  }

  // --------------------------------- Banner
  .banner__inner {
    flex-direction: column;
    align-items: center;
    gap: 25px;
    margin: 25px 0;
  }

  .banner__image {
    min-height: 280px;
    max-width: 100%;
  }

  // ------------------------------- Catalog
  .breadcrumbs {
    padding: 0 20px;
  }

  .catalog {
    padding: 0 20px;
  }
}

@media (max-width: 910px) {
  // ------------------------------- Catalog Широкие карточки
  .catalog-item--column {
    .product-item__title {
      width: 180px;
      padding: 6px 0;
      font-size: 14px;
    }
    .product-item__price {
      font-size: 20px;
    }
    .catalog-item__bottom {
      padding: 0 10px;
    }
  }
}

@media (max-width: 768px) {
  // -------------------------------------- Catalog
  .catalog-item--column {
    height: 300px;
  }

  .catalog__filter {
    justify-content: flex-end;
    gap: 30px;
  }

  .catalog__filter-btn {
    justify-content: flex-end;
  }

  .catalog-item--column,
  .catalog-item__top {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0;
  }

  .catalog-item--column .catalog-item__bottom {
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
  }

  .product-item__favorite {
    top: 6px;
  }

  .product-item__notify-link {
    bottom: 10px;
    left: 16px;
  }

  .product-item__title {
    width: auto;
    margin: 0;
  }

  .catalog-item--column .product-item__price {
    font-size: 18px;
  }

  .catalog__filter-item {
    display: none;
  }
}

@media (max-width: 666px) {
  // --------------------------------Header
  .header__top {
    min-height: 60px;
  }

  .header__top-inner {
    gap: 6px;
  }

  .page__wrapper {
    padding: 0;
  }

  .header__adress {
    display: none;
  }

  .logo {
    width: 100px;
    height: 50px;
    margin: 0;
  }
}

@media (max-width: 640px) {
  // ---------------------------------- Catalog
  .catalog__inner {
    flex-direction: column;
  }

  .catalog__left-column {
    display: none;
  }

  .catalog__left-column--show {
    display: block;
    width: 100%;
    margin-bottom: 50px;
  }

  .catalog__right-column {
    width: 100%;
  }

  .catalog__inner-aside {
    width: 100%;
    height: 100%;
  }

  .catalog__inner-list {
    justify-content: space-between;
  }

  .catalog-item--column .product-item__price {
    font-size: 14px;
  }

  .btn__filter-show {
    display: block;
  }
}

@media (max-width: 600px) {
  // --------------------------------- Banner
  .banner__slider-list {
    max-width: 360px;
  }

  .banner__image {
    max-width: 360px;
    min-height: 190px;
  }

  // ----------------------------------------- Catalog
  .catalog__inner-list {
    justify-content: center;
  }
}

@media (max-width: 359px) {
  // --------------------------------- Banner
  .banner__slider-list {
    max-width: 320px;
  }

  .banner__image {
    max-width: 320px;
    min-height: 164px;
  }
}

@media (max-width: 420px) {
  .search__content-input {
    border-radius: 0;
  }

  .search__content-button {
    margin: 0;
    font-size: 0;
    line-height: 0;
    border-radius: 0;
    color: transparent;
    background-color: $gray-bg;
    background-image: url(../img/sprite/search.svg);
    background-repeat: no-repeat;
    background-position: center;
    &:hover {
      background-color: rgba($blue, 0.3);
    }
    &:active {
      background-color: rgba($blue, 0.6);
    }
  }

  // ------------------------------------ catalog
  .catalog__filter {
    gap: 6px;
  }

  .select-css {
    margin-right: 6px;
  }
}
